	module.exports = {"data":{"markdownRemark":{"html":"<h1>Designing Voice User Interfaces</h1>\n<h2>Приемы</h2>\n<h3>Confirmation</h3>\n<h4>3-tiered confidence</h4>\n<p>Устанавливаем 3 уровня уверенности в ответе пользователя. Например, 0-45, 45-80, 80-100.</p>\n<p>В этом случае, если мы уверены в ответе на 80% и больше - не спрашиваем подтверждения (\"хорошо, заказываю пиццу\"). </p>\n<p>Если уверены на 45-80%, спрашиваем подтверждения, что поняли правильно (\"кажется, вы хотите заказать пиццу, я правильно поняла?\"). </p>\n<p>Уверены меньше, чем на 45% - переспрашиваем (\"простите, я вас не поняла, что бы вы хотели заказать?\"). </p>\n<h4>Nonspeech confirmation</h4>\n<p>ЕСли юзер просит включить свет, то сам факт включения света уже будет подтверждением, что система вас правильно поняла. ПРоизносить подтверждение стоит только в случае, когда перед включением света проходит ощутимая пауза и пользователь может начать беспокоиться, правильно ли его поняли.</p>\n<h4>Landmarking</h4>\n<p>При входе в каждый элемент меню проигрывается специфичный звук. Например, если зашли в меню информации о пробках, то звучит автомобильный гудок, зашли в главное меню - короткая приветственная мелодия.</p>\n<h3>Conversational markers</h3>\n<p>Это такие короткие фразы в диалоге, которые помогают пользвателю понять, где он находится в рамках диалога и поняла ли его система.</p>\n<p>Маркеры бывают:</p>\n<ul>\n<li>временные: \"первый вопрос\", \"мы уже почти закончили\", \"и наконец\"</li>\n<li>подтверждения: \"Спасибо!\", \"Поняла вас!\", или \"Хорошо, записала!\"</li>\n<li>позитивный фидбэк: \"Отличная работа\", или \"Рада это слышать\"</li>\n</ul>","frontmatter":{"path":"/blog/books/voice-user-interfaces","title":"Designing Voice User Interfaces"}}},"pathContext":{}}

